import app from '../app';
import chai from 'chai';
import chaiHttp from 'chai-http';
import expect from 'chai';

chai.use(chaiHttp);
const should = chai.should();

describe('Test all users APIs', () => {
  describe('/POST route', () => {
    const new_user = {
      username: 'john',
      email: 'johndoe@example.com',
      password: 'chairate'
    };
    it('should return successfull with status 200', (done) => {
      chai.request(app)
        .post('/api/v1/auth/signup')
        .send(new_user)
        .end((err, res) => {
          // expect(res.status()).to.be.equal(200);
          res.should.have.status(201);
          res.body.should.be.an('object');
          res.body.should.have.property('user');
          done();
        });
    });

    it('should retur with 400 status code', (done) => {
      chai.request(app)
        .post('/api/v1/auth/signup')
        .send()
        .end((err, res) => {
          res.should.have.status(400);
          res.body.should.be.an('object');
          res.body.should.have.property('message');
          done();
        });
    });
  });

  describe('/POST route login user', () => {
    const user = {
      firstname: 'Daniel',
      email: 'maildaniel.me1@gmail.com',
      password: 'passwordsample'
    };
    const notUser = {
      email: 'wrong',
      password: '419@gmail.com'
    };
    it('shoule return welcome with username and 200 status code', (done) => {
      chai.request(app)
        .post('/api/v1/auth/login')
        .send(user)
        .end((err, res) => {
          res.should.have.status(200);
          res.body.should.be.an('object');
        });
      done();
    });

    it('should return please sign up with 401 status code', (done) => {
      chai.request(app)
        .post('/api/v1/auth/login')
        .send(notUser)
        .end((err, res) => {
          res.should.have.status(401);
          res.body.should.be.a('string');
          res.body.should.be.eql('please sign up');
        });
      done();
    });
  });

  describe('/POST route create user business', () => {
    const newUser = {
      firstname: 'Daniel',
      lastname: 'Adek',
      businessname: 'codehub',
      location: 'newYork',
      category: 'programming',
      email: 'maildaniel.me1@gmail.com',
      password: 'passwordsample'
    };
    const nodata = {};
    it('should create business and return 201 status code', (done) => {
      chai.request(app)
        .post('/api/v1/business')
        .send(newUser)
        .end((err, res) => {
          res.should.have.status(201);
          res.body.should.be.an('object');
        });
      done();
    });

    it('should not create business but return 401 status code', (done) => {
      chai.request(app)
        .post('/api/v1/business')
        .send(nodata)
        .end((err, res) => {
          res.should.have.status(400);
          res.body.should.be.a('object');
        });
      done();
    });
  });
});
